import React, { useState, useContext } from 'react'
import classNames from 'classnames'
import PropTypes from 'prop-types'

import ContextMenu from './PhotoSeries/views/controls/ContextMenu'
import Toolbar from '@material-ui/core/Toolbar'
import Drawer from '@material-ui/core/Drawer'
import Divider from '@material-ui/core/Divider'
import IconButton from '@material-ui/core/IconButton'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'
import MenuIcon from '@material-ui/icons/Menu'
import AppLogo from '../icons/AppLogo'
import { AppContext } from '../contexts'

import ExitToAppIcon from '@material-ui/icons/ExitToApp'
import AccountCircleIcon from '@material-ui/icons/AccountCircle'

// import MenuIcon from '@material-ui/core/Menu'
import { makeStyles } from '@material-ui/core/styles'

const style = theme => ({
  root: {
    padding: '2px 4px',
    display: 'flex',
    alignItems: 'center',
    width: '100%',
    background: theme.palette.primary.main,
    color: theme.palette.primary.contrastText
  },
  searchIcon: {
    opacity: 0.3,
    marginRight: theme.spacing(1)
  },
  transparent: {
    background: 'none',
    color: theme.palette.text.primary
  },
  input: {
    background: '#FFFFFF',
    border: '1px solid #AAAAAA',
    borderRadius: theme.spacing(1) * 2,
    paddingLeft: theme.spacing(1),
    marginLeft: 0,
    flex: 1
  },
  children: {
    flexGrow: 1
  },
  iconButton: {
    padding: 10
  },
  logo: {
    height: 40,
    width: 40
  },
  divider: {
    width: 1,
    height: 28,
    margin: 4
  }
})
const useStyles = makeStyles(style)

const TopBar = ({ className, children, isTransparent, menuItems }) => {
  const classes = useStyles()
  const [contextEl, setContextEl] = useState()
  const { signOut, signedIn, forceSignIn } = useContext(AppContext)

  let finalMenuItems = []
  if (menuItems) {
    finalMenuItems = [ ...menuItems, { divider: true } ]
  }

  //   -            <List>
  // -              {signedIn && (
  // -                <ListItem button onClick={signOut}>
  // -                  <ListItemIcon>
  // -                    <ExitToAppIcon />
  // -                  </ListItemIcon>
  // -                  <ListItemText primary='Sign Out' />
  // -                </ListItem>
  // -              )}
  // -            </List>
  return (
    <Toolbar className={classNames(classes.root, className, isTransparent ? classes.transparent : null)} disableGutters>
      <IconButton className={classes.iconButton} color='inherit'>
        <AppLogo className={classes.logo} />
      </IconButton>
      <div className={classes.children}>
        {children}
      </div>
      {signedIn && (
        <IconButton
          color='inherit'
          className={classes.iconButton}
          aria-label='Menu'
          onClick={event => setContextEl(event.currentTarget)}
          // onClick={() => setDrawerOpen(!drawerOpen)}
        >
          <MenuIcon color='inherit' />
        </IconButton>
      )}
      {!signedIn && (
        <IconButton
          color='primary'
          className={classes.iconButton}
          aria-label='Menu'
          onClick={forceSignIn}
        >
          <AccountCircleIcon color='primary' />
        </IconButton>
      )}

      <ContextMenu
        anchorEl={contextEl}
        onClose={() => setContextEl(null)}
        menuItems={[
          ...finalMenuItems,
          {
            text: 'Sign Out',
            icon: ExitToAppIcon,
            action: signOut
          }
        ]}
      />

    </Toolbar>
  )
}

TopBar.propTypes = {
  className: PropTypes.string,
  children: PropTypes.any,
  isTransparent: PropTypes.bool,
  menuItems: PropTypes.arrayOf(PropTypes.shape({
    callback: PropTypes.func,
    text: PropTypes.string,
    icon: PropTypes.any,
    color: PropTypes.string
  }))
}

export default TopBar
